.navbar {
    width: 100%;
    height: 50px;
    background-color: transparent;
    position: fixed;
    z-index: 2;
    -webkit-transition: all ease-out .5s;
    -moz-transition: all ease-out .5s;
    -o-transition: all ease-out .5s;
    transition: all ease-out .5s;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.phoneNavbar {
    z-index: 2;
    width: 100%;
    height: 50px;
    background-color: transparent;
    position: fixed;
}

.active {
    height: 50px;
    position: fixed;
    background-color: #3b6d70;
}


.sliderImg {
    height: 660px;
    width: 100%;
    position: relative;
}

.description {
    width: 100%;
    position: absolute;
    top: 235px;
    color: white;
}

.description h1 {
    margin: 5px;
    font-size:  xxx-large;
    letter-spacing: 2px;
}

.description h2 {
    margin: 5px;
    font-size:  xx-large;
    letter-spacing: 2px;
}

@media (max-width: 600px) {
    .cntButtons {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 0 20px;
    }
    .sliderImg {
        display: none;
    }

    .navbar {
        display: none;
    }

    .phoneNavbar {
        display: block !important;
    }

    .active {
        background-color: #8e0000;
    }

    .phoneImg {
        display: block !important;
        width: 100%;
        height: 400px;
    }

    .description {
        width: 100%;
        border-radius: 4px;
        position: absolute;
        top: 115px;
        color: white;
    }

    .description h1 {
        margin: 5px;
        font-size: xx-large;
        letter-spacing: 2px;
    }

    .description h2 {
        margin: 5px;
        font-size: x-large;
        letter-spacing: 2px;
    }
}

@media (max-width: 1920px) {

    .phoneImg {
        display: none;
    }
}